
import { computed, defineComponent, ref } from "vue"
import { Button, Cell, Stepper, Toast } from "vant"
import OrderService from "@/services/order"
import { useRouter } from "vue-router"
import TokenContext from "@/services/token"

export default defineComponent({
  name: "ProductCheckout",
  components: {
    [Cell.name]: Cell,
    [Stepper.name]: Stepper,
    [Button.name]: Button,
  },
  props: {
    pid: String,
    productname: String,
    yujishouyi: {
      type: [String, Number],
      default: 0,
    },
    productprice: {
      type: [String, Number],
      default: 0,
    },
    shengyukucun: {
      type: [String, Number],
      default: 0,
    },
  },
  setup(props) {
    const { push } = useRouter()
    const loading = ref(false)
    const quantity = ref(1)

    const totalPrice = computed(() => quantity.value * Number(props.productprice))

    function createOrder() {
      if (!TokenContext.existToken()) {
        Toast({
          message: "请先登录",
          duration: 1000,
          onClose: () => {
            push("/login")
          },
        })
        return
      }

      loading.value = true
      OrderService.createOrder({
        productId: props.pid,
        num: quantity.value,
      })
        .then(({ status, message, data }) => {
          Toast(message)
          if (status === 200) {
            const { orderNo } = data
            push(`/orders/${orderNo}`)
          }
        })
        .finally(() => {
          loading.value = false
        })
    }

    return {
      loading,
      quantity,
      totalPrice,
      createOrder,
    }
  },
})
