<template>
  <div class="product-detail-card">
    <div class="product-detail-card__l1">
      <div class="product-detail-card__rate-of-return">
        <div class="product-detail-card__rate-of-return__value">{{ yujishouyi }}</div>
        <div class="product-detail-card__rate-of-return__subtitle">预期回报率</div>
      </div>
      <div class="product-detail-card__info">
        <div class="product-detail-card__cell">
          <div class="product-detail-card__cell__title">租金：</div>
          <div class="product-detail-card__cell__value">{{ productprice }}USDT/份</div>
        </div>
        <div class="product-detail-card__cell">
          <div class="product-detail-card__cell__title">产品周期：</div>
          <div class="product-detail-card__cell__value">{{ chanpinzhouqi }}天</div>
        </div>
        <div class="product-detail-card__cell">
          <div class="product-detail-card__cell__title">电费：</div>
          <div class="product-detail-card__cell__value">{{ dianfei }}</div>
        </div>
      </div>
    </div>
    <div class="product-detail-card__l2">
      <van-progress :percentage="percentage" :show-pivot="false" color="#8ddbff" />
      <div class="product-detail-card__quantity">剩余{{ shengyukucun }}份</div>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, ref } from "vue"
import { NavBar, Progress } from "vant"

export default defineComponent({
  name: "ProductDetailCard",
  components: {
    [NavBar.name]: NavBar,
    [Progress.name]: Progress,
  },
  props: {
    productprice: {
      type: [String, Number],
      default: 0,
    },
    yujishouyi: {
      type: [String, Number],
      default: 0,
    },
    kucun: {
      type: [String, Number],
      default: 0,
    },
    shengyukucun: {
      type: [String, Number],
      default: 0,
    },
    dianfei: {
      type: [String, Number],
      default: 0,
    },
    chanpinzhouqi: {
      type: [String, Number],
      default: 0,
    },
  },
  setup(props) {
    const active = ref(0)

    const percentage = computed(() => {
      const shengyukucun = props.shengyukucun ?? 0
      const kucun = props.kucun ?? 0
      const value = (Number(shengyukucun) / Number(kucun)) * 100
      return value ?? 0
    })
    return { active, percentage }
  },
})
</script>
