<template>
  <div class="product-detail">
    <van-nav-bar title="共享算力详情" left-arrow :border="false" @click-left="back()" />
    <div class="product-detail__content">
      <product-detail-card
        :productprice="product.productprice"
        :yujishouyi="product.yujishouyi"
        :kucun="product.kucun"
        :shengyukucun="product.shengyukucun"
        :dianfei="product.dianfei"
        :chanpinzhouqi="product.chanpinzhouqi"
      />
      <product-detail-output-rule
        :goumairiqi="product.goumairiqi"
        :shangjiariqi="product.shangjiariqi"
        :fuwuriqi="product.fuwuriqi"
      />
      <product-detail-body
        :productname="product.productname"
        :currencytype="product.currencytype"
        :unit="product.unit"
        :yujishouyi="product.yujishouyi"
        :rishouyi="product.rishouyi"
        :chanpinzhouqi="product.chanpinzhouqi"
        :expense="product.expense"
        :fenhongtianshu="product.fenhongtianshu"
        :chanpinxiangqing="product.chanpinxiangqing"
        :fengxiantixing="product.fengxiantixing"
      />
    </div>
    <bottom-logo />
    <van-popup v-model:show="checkoutPopupShow" position="bottom" round closeable>
      <product-checkout
        :pid="product.pid"
        :productname="product.productname"
        :productprice="product.productprice"
        :yujishouyi="product.yujishouyi"
        :unit="product.unit"
        :shengyukucun="product.shengyukucun"
      />
    </van-popup>
    <div class="product-detail__actions__placeholder" />
    <div class="product-detail__actions" @click="checkoutPopupShow = true">立即购买</div>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref, watch } from "vue"
import { NavBar, Popup, Toast } from "vant"
import ProductDetailCard from "@/views/product/ProductDetailCard.vue"
import ProductDetailOutputRule from "@/views/product/ProductDetailOutputRule.vue"
import ProductDetailBody from "@/views/product/ProductDetailBody.vue"
import { useRouter } from "vue-router"
import ProductService, { Product } from "@/services/product"
import BottomLogo from "@/components/BottomLogo.vue"
import ProductCheckout from "@/views/product/ProductCheckout.vue"

export default defineComponent({
  name: "ProductDetail",
  components: {
    ProductCheckout,
    BottomLogo,
    ProductDetailBody,
    ProductDetailOutputRule,
    ProductDetailCard,
    [NavBar.name]: NavBar,
    [Popup.name]: Popup,
  },
  props: {
    id: String,
  },
  setup(props) {
    const { back } = useRouter()
    const loading = ref(false)
    const checkoutPopupShow = ref(false)
    const product = ref<Product>({})

    function refreshProduct() {
      if (!props.id) {
        return
      }
      loading.value = true
      const loadingToast = Toast({
        type: "loading",
        forbidClick: true,
        loadingType: "spinner",
      })
      ProductService.getProduct(props.id)
        .then(({ data }) => (product.value = data))
        .finally(() => {
          loading.value = false
          loadingToast.clear()
        })
    }

    onMounted(refreshProduct)
    watch(() => props.id, refreshProduct)

    return {
      back,
      loading,
      checkoutPopupShow,
      product,
    }
  },
})
</script>
