<template>
  <div class="product-checkout">
    <van-cell title-class="product-checkout__title" :title="productname" :border="false" center />
    <van-cell title-class="product-checkout__rate-of-return" :border="false">
      <template #title>
        <div class="product-checkout__rate-of-return__title">年化收益率</div>
        <div class="product-checkout__rate-of-return__value">{{ yujishouyi }}</div>
      </template>
    </van-cell>
    <van-cell title-class="product-checkout__rent" :border="false">
      <template #title>
        <div class="product-checkout__rent__title">单价</div>
        <div class="product-checkout__rent__value">{{ productprice }}</div>
        <div class="product-checkout__rent__currency">USDT/份</div>
      </template>
    </van-cell>
    <van-cell title="购买数量" border>
      <template #default>
        <van-stepper v-model="quantity" :max="shengyukucun" integer></van-stepper>
      </template>
    </van-cell>
    <van-cell center>
      <template #title>
        <span>总价</span>
        <span class="product-checkout__total">{{ totalPrice }}</span>
        <span>USDT</span>
      </template>
      <template #default>
        <van-button
          class="product-checkout__submit"
          round
          type="primary"
          :loading="loading"
          @click="createOrder"
        >
          提交订单
        </van-button>
      </template>
    </van-cell>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, ref } from "vue"
import { Button, Cell, Stepper, Toast } from "vant"
import OrderService from "@/services/order"
import { useRouter } from "vue-router"
import TokenContext from "@/services/token"

export default defineComponent({
  name: "ProductCheckout",
  components: {
    [Cell.name]: Cell,
    [Stepper.name]: Stepper,
    [Button.name]: Button,
  },
  props: {
    pid: String,
    productname: String,
    yujishouyi: {
      type: [String, Number],
      default: 0,
    },
    productprice: {
      type: [String, Number],
      default: 0,
    },
    shengyukucun: {
      type: [String, Number],
      default: 0,
    },
  },
  setup(props) {
    const { push } = useRouter()
    const loading = ref(false)
    const quantity = ref(1)

    const totalPrice = computed(() => quantity.value * Number(props.productprice))

    function createOrder() {
      if (!TokenContext.existToken()) {
        Toast({
          message: "请先登录",
          duration: 1000,
          onClose: () => {
            push("/login")
          },
        })
        return
      }

      loading.value = true
      OrderService.createOrder({
        productId: props.pid,
        num: quantity.value,
      })
        .then(({ status, message, data }) => {
          Toast(message)
          if (status === 200) {
            const { orderNo } = data
            push(`/orders/${orderNo}`)
          }
        })
        .finally(() => {
          loading.value = false
        })
    }

    return {
      loading,
      quantity,
      totalPrice,
      createOrder,
    }
  },
})
</script>

