
import { computed, defineComponent, ref } from "vue"
import { NavBar, Progress } from "vant"

export default defineComponent({
  name: "ProductDetailCard",
  components: {
    [NavBar.name]: NavBar,
    [Progress.name]: Progress,
  },
  props: {
    productprice: {
      type: [String, Number],
      default: 0,
    },
    yujishouyi: {
      type: [String, Number],
      default: 0,
    },
    kucun: {
      type: [String, Number],
      default: 0,
    },
    shengyukucun: {
      type: [String, Number],
      default: 0,
    },
    dianfei: {
      type: [String, Number],
      default: 0,
    },
    chanpinzhouqi: {
      type: [String, Number],
      default: 0,
    },
  },
  setup(props) {
    const active = ref(0)

    const percentage = computed(() => {
      const shengyukucun = props.shengyukucun ?? 0
      const kucun = props.kucun ?? 0
      const value = (Number(shengyukucun) / Number(kucun)) * 100
      return value ?? 0
    })
    return { active, percentage }
  },
})
