import client, { ClientResponse } from "@/services/client"

interface OrderCreateForm {
  productId?: string
  num?: number
}

interface OrderPayForm {
  orderNo?: string
  paypassword?: string
}

interface OrderQuery {
  status?: string
}

export interface Order {
  orderNo?: string
  productname?: string
  producttype?: string
  currencytype?: string
  status?: string
  number?: string
  zhifushuliang?: string
  fuwuzhouqi?: string
  fuwuriqitian?: string
  chuangjiantime?: string
}

export interface OrderCount {
  arrearage?: number
  paid?: number
  end?: number
}

export interface orderDetail {
  id?: string
  uid?: string
  order_id?: string
  order_no?: string
  currencytype?: string
  suanli?: string
  status?: string
  money?: string
  chuangjiantime?: string
  updatetime?: string
}

export default class OrderService {
  public static createOrder(form: OrderCreateForm): Promise<ClientResponse<Order>> {
    return client.post("/orders/create.php", form).then(({ data }) => data)
  }

  public static payOrder(form: OrderPayForm): Promise<ClientResponse<Order>> {
    return client.post("/orders/pay.php", form).then(({ data }) => data)
  }

  public static getOrder(orderId?: string): Promise<ClientResponse<Order>> {
    return client.get(`/orders/list.php?orderNo=${orderId}`).then(({ data }) => data)
  }

  public static cancelOrder(orderNo?: string): Promise<ClientResponse<void>> {
    return client
      .post(`/orders/cancel.php?`, {
        orderNo,
      })
      .then(({ data }) => data)
  }

  public static getOrders(query: OrderQuery): Promise<ClientResponse<Order[]>> {
    return client.get(`/orders/list.php`, { params: query }).then(({ data }) => data)
  }

  public static getDetailOrders(): Promise<ClientResponse<orderDetail[]>> {
    return client.get("/orders/message.php").then(({ data }) => data)
  }

  public static countOrders(): Promise<ClientResponse<OrderCount>> {
    return client.get(`/orders/count.php`).then(({ data }) => data)
  }
}
